import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserService } from '../../services/user.service';
import { Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'app-admin-vendeurs',
  templateUrl: './admin-vendeurs.component.html',
  styleUrls: []
})
export class AdminVendeursComponent implements OnInit, OnDestroy {
  currentUser: any = null;
  sellers: any[] = [];
  allSellers: any[] = []; // Store all sellers for filtering
  loadingSellers = false;
  private routerSubscription: Subscription = new Subscription();

  // Pagination properties
  currentPage = 1;
  itemsPerPage = 8;
  totalPages = 1;
  totalSellers = 0;

  // Search properties
  searchQuery = '';
  selectedFilter = 'all'; // 'all', 'vip', 'simple'

  constructor(private userService: UserService, private router: Router) {}

  ngOnInit() {
    this.loadCurrentUser();
    this.loadSellers();

    // Listen to route changes to reload sellers when navigating to this page
    this.routerSubscription = this.router.events
      .pipe(filter((event): event is NavigationEnd => event instanceof NavigationEnd))
      .subscribe((event) => {
        if (event.url === '/admin/vendeurs') {
          this.loadSellers();
        }
      });
  }

  ngOnDestroy() {
    this.routerSubscription.unsubscribe();
  }

  loadCurrentUser() {
    this.userService.getCurrentUser().subscribe({
      next: (response) => {
        this.currentUser = response.user;
      },
      error: () => {
        this.router.navigate(['/login']);
      }
    });
  }

  loadSellers(page = 1) {
    console.log(' Dbut du chargement des vendeurs - Page:', page, 'Limit:', this.itemsPerPage);
    this.loadingSellers = true;
    this.userService.getAllSellers(page, this.itemsPerPage).subscribe({
      next: (response) => {
        this.loadingSellers = false;
        this.sellers = response.sellers || [];
        this.allSellers = response.sellers || []; // For frontend filtering

        // Update pagination info from backend
        this.currentPage = response.pagination?.page || 1;
        this.totalSellers = response.pagination?.total || 0;
        this.totalPages = response.pagination?.totalPages || 1;

        console.log(' Vendeurs chargs avec succs:', this.sellers.length, 'vendeurs affichs sur', this.totalSellers, 'total');
        console.log(' Pagination - Page:', this.currentPage, 'Total pages:', this.totalPages, 'Total vendeurs:', this.totalSellers);
        console.log(' Dtails des vendeurs:', this.sellers);
      },
      error: (error) => {
        this.loadingSellers = false;
        console.error(' Erreur lors du chargement des vendeurs:', error);
        console.error(' Dtails de l\'erreur:', error.error || error.message);
        this.sellers = [];
        this.allSellers = [];
      }
    });
  }

  applyFilters() {
    let filteredSellers = [...this.allSellers];

    // Apply type filter
    if (this.selectedFilter === 'vip') {
      filteredSellers = filteredSellers.filter(seller => seller.credits > 0);
    } else if (this.selectedFilter === 'simple') {
      filteredSellers = filteredSellers.filter(seller => seller.credits === 0);
    }
    // 'all' shows all sellers

    // Apply search filter
    if (this.searchQuery && this.searchQuery.length >= 2) {
      filteredSellers = filteredSellers.filter(seller =>
        seller.name.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    }

    // Calculate pagination
    this.totalSellers = filteredSellers.length;
    this.totalPages = Math.ceil(this.totalSellers / this.itemsPerPage);

    // Reset to first page if current page is out of bounds
    if (this.currentPage > this.totalPages) {
      this.currentPage = 1;
    }

    // Get sellers for current page
    const startIndex = (this.currentPage - 1) * this.itemsPerPage;
    const endIndex = startIndex + this.itemsPerPage;
    this.sellers = filteredSellers.slice(startIndex, endIndex);
  }

  logout() {
    this.userService.logout().subscribe({
      next: () => {
        this.router.navigate(['/login']);
      },
      error: (error) => {
        console.error('Logout error:', error);
        this.router.navigate(['/login']);
      }
    });
  }

  // Search method
  onSearchInput() {
    this.currentPage = 1; // Reset to first page when searching
    this.applyFilters();
  }

  // Filter method
  onFilterChange() {
    this.currentPage = 1; // Reset to first page when filtering
    this.applyFilters();
  }

  // Pagination methods
  nextPage() {
    if (this.currentPage < this.totalPages) {
      this.currentPage++;
      this.loadSellers(this.currentPage);
    }
  }

  prevPage() {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.loadSellers(this.currentPage);
    }
  }

  goToPage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.currentPage = page;
      this.loadSellers(this.currentPage);
    }
  }
}