import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, tap } from 'rxjs';
import { User } from '../models/user.model';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private readonly API_URL = 'http://localhost:3000/api/auth';
  private readonly TOKEN_KEY = 'auth_token';

  constructor(private http: HttpClient) {}

  private getHeaders(): HttpHeaders {
    const token = this.getToken();
    return new HttpHeaders({
      ...(token && { Authorization: `Bearer ${token}` })
    });
  }

  private getJsonHeaders(): HttpHeaders {
    const token = this.getToken();
    return new HttpHeaders({
      'Content-Type': 'application/json',
      ...(token && { Authorization: `Bearer ${token}` })
    });
  }

  private getToken(): string | null {
    return localStorage.getItem(this.TOKEN_KEY);
  }

  private setToken(token: string): void {
    localStorage.setItem(this.TOKEN_KEY, token);
  }

  register(userData: {
    email: string;
    password: string;
    firstName: string;
    lastName: string;
    phone?: string;
    whatsappLink?: string;
    city?: string;
    profilePhoto?: File;
  }): Observable<any> {
    const formData = new FormData();
    formData.append('email', userData.email);
    formData.append('password', userData.password);
    formData.append('firstName', userData.firstName);
    formData.append('lastName', userData.lastName);
    if (userData.phone) formData.append('phone', userData.phone);
    if (userData.whatsappLink) formData.append('whatsappLink', userData.whatsappLink);
    if (userData.city) formData.append('city', userData.city);
    if (userData.profilePhoto) formData.append('profilePhoto', userData.profilePhoto);

    return this.http.post(`${this.API_URL}/register-seller`, formData, { headers: this.getHeaders() });
  }

  login(email: string, password: string): Observable<any> {
    return this.http.post(`${this.API_URL}/login`, { email, password }, { headers: this.getHeaders() })
      .pipe(
        tap((response: any) => {
          if (response.token) {
            this.setToken(response.token);
          }
        })
      );
  }

  logout(): Observable<any> {
    return this.http.post(`${this.API_URL}/logout`, {}, { headers: this.getHeaders() })
      .pipe(
        tap(() => {
          localStorage.removeItem(this.TOKEN_KEY);
        })
      );
  }

  getCurrentUser(): Observable<any> {
    return this.http.get(`${this.API_URL}/me`, { headers: this.getHeaders() });
  }

  changePassword(data: { currentPassword: string; newPassword: string }): Observable<any> {
    return this.http.put(`${this.API_URL}/change-password`, data, { headers: this.getHeaders() });
  }

  initialChangePassword(data: { email: string; newPassword: string }): Observable<any> {
    return this.http.put(`${this.API_URL}/initial-change-password`, data, { headers: this.getHeaders() });
  }

  completeFirstLogin(data: { firstName: string; lastName: string; newPassword: string }): Observable<any> {
    return this.http.post(`${this.API_URL}/complete-first-login`, data, { headers: this.getHeaders() });
  }

  isLoggedIn(): boolean {
    return !!this.getToken();
  }

  createProduct(productData: FormData): Observable<any> {
    return this.http.post('http://localhost:3000/api/auth/products', productData, { headers: this.getHeaders() });
  }

  getProducts(page: number = 1, limit: number = 8, search?: string, category?: string, city?: string, priceRange?: string): Observable<any> {
    let url = `http://localhost:3000/api/auth/products?page=${page}&limit=${limit}`;
    if (search) url += `&search=${encodeURIComponent(search)}`;
    if (category) url += `&category=${encodeURIComponent(category)}`;
    if (city) url += `&city=${encodeURIComponent(city)}`;
    if (priceRange) url += `&priceRange=${encodeURIComponent(priceRange)}`;
    return this.http.get(url, { headers: this.getJsonHeaders() });
  }

  getPendingProducts(): Observable<any> {
    return this.http.get('http://localhost:3000/api/auth/products/pending', { headers: this.getJsonHeaders() });
  }

  updateProductStatus(productId: string, status: string, isFeatured?: boolean): Observable<any> {
    const body: any = { productId, status };
    if (isFeatured !== undefined) {
      body.isFeatured = isFeatured;
    }
    return this.http.put('http://localhost:3000/api/auth/products/status', body, { headers: this.getJsonHeaders() });
  }

  getSellerProducts(): Observable<any> {
    return this.http.get('http://localhost:3000/api/auth/products/seller', { headers: this.getJsonHeaders() });
  }

  getAllSellers(page: number = 1, limit: number = 8): Observable<any> {
    return this.http.get(`http://localhost:3000/api/auth/sellers?page=${page}&limit=${limit}`, { headers: this.getJsonHeaders() });
  }

  buyCredits(amount: number, productId?: string): Observable<any> {
    const body: any = { amount };
    if (productId) {
      body.productId = productId;
    }
    return this.http.post('http://localhost:3000/api/auth/buy-credits', body, { headers: this.getJsonHeaders() });
  }

  getNotifications(page: number = 1, limit: number = 4): Observable<any> {
    return this.http.get(`http://localhost:3000/api/auth/notifications?page=${page}&limit=${limit}`, { headers: this.getJsonHeaders() });
  }

  markNotificationAsRead(notificationId: string): Observable<any> {
    return this.http.put(`http://localhost:3000/api/auth/notifications/${notificationId}/read`, {}, { headers: this.getJsonHeaders() });
  }

  getUnreadNotificationsCount(): Observable<any> {
    return this.http.get('http://localhost:3000/api/auth/notifications/unread-count', { headers: this.getJsonHeaders() });
  }

  resetUnreadNotificationsCount(): Observable<any> {
    return this.http.put('http://localhost:3000/api/auth/notifications/reset-unread-count', {}, { headers: this.getJsonHeaders() });
  }
}