export interface Product {
  id: string;
  name: string;
  description: string;
  price: number;
  category: ProductCategory;
  status: ProductStatus;
  sellerId: string;
  seller: {
    id: string;
    firstName: string;
    lastName: string;
    email: string;
    phone?: string;
    whatsappLink?: string;
  };
  photos: Photo[];
  createdAt: Date;
  updatedAt: Date;
  isFeatured?: boolean;
}

export interface Photo {
  id: string;
  productId: string;
  url: string;
  type: 'MAIN' | 'SECONDARY';
  order: number;
  createdAt: Date;
}

export type ProductCategory =
  | 'AGROALIMENTAIRE'
  | 'ANIMAUX'
  | 'IMMOBILIER'
  | 'MAISON'
  | 'EQUIPEMENT'
  | 'MODE_BEAUTE'
  | 'MULTIMEDIA'
  | 'SPORT_LOISIRS'
  | 'VEHICULES'
  | 'AUTRE';

export type ProductStatus = 'PENDING' | 'APPROVED' | 'REJECTED';

