import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UserService } from '../../services/user.service';
import { Router } from '@angular/router';
import { Product, PRODUCT_CATEGORIES } from '../../models/product.model';
import { User } from '../../models/user.model';

interface SellerProduct {
  id: string;
  name: string;
  description: string;
  price: number;
  category: string;
  status: string;
  mainPhoto: string;
  photos: Array<{
    id: string;
    url: string;
    type: string;
    order: number;
  }>;
  createdAt: string;
  isFeatured?: boolean;
  vipStatus?: number;
  featuredUntil?: string;
}

@Component({
  selector: 'app-dashboard-vendeur',
  templateUrl: './dashboard-vendeur.component.html',
  styleUrls: []
})
export class DashboardVendeurComponent implements OnInit {
  currentUser: User | null = null;
  showAddProductForm = false;
  productForm: FormGroup;
  productCategories = PRODUCT_CATEGORIES;
  loading = false;
  sellerProducts: SellerProduct[] = [];
  allSellerProducts: SellerProduct[] = [];
  loadingProducts = false;

  // Pagination properties
  currentPage = 1;
  itemsPerPage = 6;
  totalPages = 1;
  totalProducts = 0;

  // Camera properties
  showCamera = false;
  capturedImage: string | null = null;
  cameraStream: MediaStream | null = null;
  photoCaptured = false;

  // Secondary photos properties
  secondaryPhotos: string[] = [];
  secondaryPhotoFiles: File[] = [];

  // Secondary camera properties
  showSecondaryCamera = false;
  secondaryCameraStream: MediaStream | null = null;

  // Photo modal properties
  showPhotoModal = false;
  selectedPhotoUrl: string | null = null;
  selectedPhotoAlt: string = '';

  // Credits view properties
  showCreditsView = false;
  selectedPlan: any = null;
  selectedProductForCredits: any = null;

  constructor(
    private userService: UserService,
    private router: Router,
    private fb: FormBuilder
  ) {
    this.productForm = this.fb.group({
      name: ['', [Validators.required]],
      category: ['', [Validators.required]],
      description: ['', [Validators.required]],
      price: ['', [Validators.required, Validators.min(0)]],
      mainPhoto: [null, [Validators.required]],
      secondaryPhotos: [null]
    });
  }

  ngOnInit() {
    this.loadCurrentUser();
    this.loadSellerProducts();
  }

  loadCurrentUser() {
    this.userService.getCurrentUser().subscribe({
      next: (response) => {
        this.currentUser = response.user;
      },
      error: () => {
        this.router.navigate(['/login']);
      }
    });
  }

  logout() {
    this.userService.logout().subscribe({
      next: () => {
        this.router.navigate(['/login']);
      },
      error: () => {
        // Even if logout fails, clear local state
        localStorage.removeItem('auth_token');
        this.router.navigate(['/login']);
      }
    });
  }

  buyCredits() {
    this.showCreditsView = true;
    this.showAddProductForm = false; // Hide add product form if open
  }

  selectCreditPlan(plan: any) {
    this.selectedPlan = plan;
  }

  selectProductForCredits(product: any) {
    this.selectedProductForCredits = product;
  }

  purchaseCredits() {
    if (!this.selectedPlan || !this.selectedProductForCredits) {
      alert('Veuillez slectionner un plan et un produit');
      return;
    }

    this.userService.buyCredits(this.selectedPlan.credits, this.selectedProductForCredits.id).subscribe({
      next: (response) => {
        // Update credits locally
        if (this.currentUser) {
          (this.currentUser as any).credits = response.user.credits;
        }
        alert(`Crdits achets avec succs! ${this.selectedPlan.credits} crdits pour le produit "${this.selectedProductForCredits.name}"`);
        this.showCreditsView = false;
        this.selectedPlan = null;
        this.selectedProductForCredits = null;
        // Refresh products to show VIP status
        this.loadSellerProducts();
      },
      error: (error) => {
        console.error('Error purchasing credits:', error);
        alert('Erreur lors de l\'achat des crdits');
      }
    });
  }

  cancelCreditsPurchase() {
    this.showCreditsView = false;
    this.selectedPlan = null;
    this.selectedProductForCredits = null;
  }


  loadSellerProducts() {
    this.loadingProducts = true;
    this.userService.getSellerProducts().subscribe({
      next: (response) => {
        this.loadingProducts = false;
        this.allSellerProducts = response.products || [];
        this.applyPagination();
      },
      error: (error) => {
        this.loadingProducts = false;
        console.error('Error loading seller products:', error);
        this.allSellerProducts = [];
        this.sellerProducts = [];
      }
    });
  }

  applyPagination() {
    this.totalProducts = this.allSellerProducts.length;
    this.totalPages = Math.ceil(this.totalProducts / this.itemsPerPage);

    // Reset to first page if current page is out of bounds
    if (this.currentPage > this.totalPages) {
      this.currentPage = 1;
    }

    // Get products for current page
    const startIndex = (this.currentPage - 1) * this.itemsPerPage;
    const endIndex = startIndex + this.itemsPerPage;
    this.sellerProducts = this.allSellerProducts.slice(startIndex, endIndex);
  }

  onSubmitProduct() {
    if (this.productForm.valid) {
      this.loading = true;

      const formData = new FormData();
      formData.append('name', this.productForm.value.name);
      formData.append('description', this.productForm.value.description);
      formData.append('price', this.productForm.value.price);
      formData.append('category', this.productForm.value.category);

      // Add main photo
      if (this.productForm.value.mainPhoto) {
        formData.append('photos', this.productForm.value.mainPhoto);
      }

      // Add secondary photos from camera captures
      if (this.secondaryPhotoFiles.length > 0) {
        for (let i = 0; i < this.secondaryPhotoFiles.length; i++) {
          formData.append('photos', this.secondaryPhotoFiles[i]);
        }
      }

      this.userService.createProduct(formData).subscribe({
        next: (response) => {
          this.loading = false;
          this.showAddProductForm = false;
          this.productForm.reset();
          this.capturedImage = null;
          this.photoCaptured = false;
          this.showCamera = false;
          this.secondaryPhotos = [];
          this.secondaryPhotoFiles = [];
          // Stop all cameras if still running
          if (this.cameraStream) {
            this.cameraStream.getTracks().forEach(track => track.stop());
            this.cameraStream = null;
          }
          if (this.secondaryCameraStream) {
            this.secondaryCameraStream.getTracks().forEach(track => track.stop());
            this.secondaryCameraStream = null;
          }
          this.loadSellerProducts(); // Refresh products list
        },
        error: (error) => {
          this.loading = false;
          console.error('Product creation error:', error);
          if (error.error && error.error.errors && Array.isArray(error.error.errors)) {
            // Validation errors from express-validator
            const errorMessages = error.error.errors.map((err: any) => err.message).join(', ');
            alert('Erreurs de validation: ' + errorMessages);
          } else if (error.error && error.error.error) {
            // Other backend errors
            alert('Erreur: ' + error.error.error);
          } else {
            alert('Erreur lors de l\'ajout du produit: ' + (error.message || 'Erreur inconnue'));
          }
        }
      });
    } else {
      alert('Veuillez remplir tous les champs obligatoires correctement.');
    }
  }

  getProductsByStatus(status: string): number {
    return this.sellerProducts.filter(product => product.status === status).length;
  }

  getVipProductsCount(): number {
    return this.sellerProducts.filter(product => product.isFeatured).length;
  }

  getDaysRemaining(featuredUntil: string): number {
    if (!featuredUntil) return 0;
    const endDate = new Date(featuredUntil);
    const today = new Date();
    const diffTime = endDate.getTime() - today.getTime();
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return Math.max(0, diffDays);
  }

  isProductCurrentlyFeatured(product: SellerProduct): boolean {
    return !!(product.vipStatus === 1 && product.featuredUntil && new Date(product.featuredUntil) > new Date());
  }

  isCurrentlyFeatured(product: SellerProduct): boolean {
    return !!(product.featuredUntil && new Date(product.featuredUntil) > new Date());
  }

  onImageError(event: any) {
    console.error('Image failed to load:', event.target.src);
    event.target.src = 'https://via.placeholder.com/300x200?text=Image+non+disponible';
  }

  // Camera methods
  startCamera() {
    this.showCamera = true;
    navigator.mediaDevices.getUserMedia({ video: true })
      .then(stream => {
        this.cameraStream = stream;
        const videoElement = document.getElementById('cameraVideo') as HTMLVideoElement;
        if (videoElement) {
          videoElement.srcObject = stream;
        }
      })
      .catch(error => {
        console.error('Error accessing camera:', error);
        alert('Erreur lors de l\'accs  la camra');
        this.showCamera = false;
      });
  }

  stopCamera() {
    this.showCamera = false;
    if (this.cameraStream) {
      this.cameraStream.getTracks().forEach((track: MediaStreamTrack) => track.stop());
      this.cameraStream = null;
    }
    // Don't reset capturedImage and photoCaptured here - keep the photo for display
  }

  capturePhoto() {
    const videoElement = document.getElementById('cameraVideo') as HTMLVideoElement;
    const canvasElement = document.getElementById('cameraCanvas') as HTMLCanvasElement;

    if (videoElement && canvasElement && videoElement.readyState >= 2) { // HAVE_CURRENT_DATA or higher
      console.log('Video dimensions:', videoElement.videoWidth, 'x', videoElement.videoHeight);
      console.log('Video ready state:', videoElement.readyState);

      const context = canvasElement.getContext('2d');
      if (context) {
        // Set canvas size to match video
        canvasElement.width = videoElement.videoWidth || 640;
        canvasElement.height = videoElement.videoHeight || 480;

        // Draw the current video frame to canvas
        try {
          context.drawImage(videoElement, 0, 0, canvasElement.width, canvasElement.height);
          console.log('Image drawn to canvas');

          // Create data URL for immediate preview
          this.capturedImage = canvasElement.toDataURL('image/jpeg');
          this.photoCaptured = true;

          // Convert canvas to blob and create file
          canvasElement.toBlob((blob) => {
            if (blob) {
              console.log('Blob created, size:', blob.size);
              const file = new File([blob], 'captured-photo.jpg', { type: 'image/jpeg' });

              // Update form control
              this.productForm.patchValue({
                mainPhoto: file
              });

              console.log('Photo captured successfully');
            } else {
              console.error('Failed to create blob from canvas');
            }
          }, 'image/jpeg', 0.9); // High quality JPEG
        } catch (error) {
          console.error('Error drawing to canvas:', error);
        }
      }
    } else {
      console.error('Video not ready or elements not found. Ready state:', videoElement?.readyState);
    }
  }

  confirmPhoto() {
    this.showCamera = false;
    if (this.cameraStream) {
      this.cameraStream.getTracks().forEach((track: MediaStreamTrack) => track.stop());
      this.cameraStream = null;
    }
  }

  retakePhoto() {
    this.capturedImage = null;
  }

  removeCapturedImage() {
    this.capturedImage = null;
    this.photoCaptured = false;
    this.productForm.patchValue({
      mainPhoto: null
    });
  }

  getVideoDimensions(): string {
    const videoElement = document.getElementById('cameraVideo') as HTMLVideoElement;
    if (videoElement) {
      return `${videoElement.videoWidth || 0} x ${videoElement.videoHeight || 0}`;
    }
    return 'N/A';
  }

  // Secondary photos methods
  addSecondaryPhoto() {
    this.startSecondaryCamera();
  }

  startSecondaryCamera() {
    this.showSecondaryCamera = true;
    navigator.mediaDevices.getUserMedia({ video: true })
      .then(stream => {
        this.secondaryCameraStream = stream;
        const videoElement = document.getElementById('secondaryCameraVideo') as HTMLVideoElement;
        if (videoElement) {
          videoElement.srcObject = stream;
        }
      })
      .catch(error => {
        console.error('Error accessing camera:', error);
        alert('Erreur lors de l\'accs  la camra');
        this.showSecondaryCamera = false;
      });
  }

  captureSecondaryPhoto() {
    const videoElement = document.getElementById('secondaryCameraVideo') as HTMLVideoElement;
    const canvasElement = document.getElementById('secondaryCameraCanvas') as HTMLCanvasElement;

    if (videoElement && canvasElement && this.secondaryPhotos.length < 3) {
      const context = canvasElement.getContext('2d');
      if (context) {
        canvasElement.width = videoElement.videoWidth;
        canvasElement.height = videoElement.videoHeight;
        context.drawImage(videoElement, 0, 0);
        const imageUrl = canvasElement.toDataURL('image/jpeg');
        const file = new File([this.dataURLToBlob(imageUrl)], `secondary-photo-${this.secondaryPhotos.length + 1}.jpg`, { type: 'image/jpeg' });

        this.secondaryPhotos.push(imageUrl);
        this.secondaryPhotoFiles.push(file);
      }
    }
    this.stopSecondaryCamera();
  }

  stopSecondaryCamera() {
    this.showSecondaryCamera = false;
    if (this.secondaryCameraStream) {
      this.secondaryCameraStream.getTracks().forEach((track: MediaStreamTrack) => track.stop());
      this.secondaryCameraStream = null;
    }
  }

  removeSecondaryPhoto(index: number) {
    this.secondaryPhotos.splice(index, 1);
    this.secondaryPhotoFiles.splice(index, 1);
  }

  dataURLToBlob(dataURL: string): Blob {
    const arr = dataURL.split(',');
    const mime = arr[0].match(/:(.*?);/)![1];
    const bstr = atob(arr[1]);
    let n = bstr.length;
    const u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], { type: mime });
  }

  // Photo modal methods
  openPhotoModal(photoUrl: string, alt: string) {
    this.selectedPhotoUrl = photoUrl;
    this.selectedPhotoAlt = alt;
    this.showPhotoModal = true;
  }

  closePhotoModal() {
    this.showPhotoModal = false;
    this.selectedPhotoUrl = null;
    this.selectedPhotoAlt = '';
  }

  // Pagination methods
  nextPage() {
    if (this.currentPage < this.totalPages) {
      this.currentPage++;
      this.applyPagination();
    }
  }

  prevPage() {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.applyPagination();
    }
  }

  goToPage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.currentPage = page;
      this.applyPagination();
    }
  }
}