import { Component, OnInit } from '@angular/core';
import { Product } from '../../models/product.model';
import { UserService } from '../../services/user.service';
import { Router } from '@angular/router';

interface CreditPlan {
  credits: number;
  duration: number; // in months
}

@Component({
  selector: 'app-buy-credits',
  templateUrl: './buy-credits.component.html',
  styleUrls: ['./buy-credits.component.scss']
})
export class BuyCreditsComponent implements OnInit {
  userProducts: Product[] = [];
  selectedPlan: CreditPlan | null = null;
  selectedProduct: Product | null = null;
  currentUserCredits: number = 0;

  constructor(
    private userService: UserService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.loadUserProducts();
    this.loadCurrentUserCredits();
  }

  loadUserProducts(): void {
    this.userService.getSellerProducts().subscribe({
      next: (response: any) => {
        this.userProducts = response.products || [];
      },
      error: (error) => {
        console.error('Error loading user products:', error);
        this.userProducts = [];
      }
    });
  }

  loadCurrentUserCredits(): void {
    this.userService.getCurrentUser().subscribe({
      next: (response: any) => {
        this.currentUserCredits = response.user?.credits || 0;
      },
      error: (error) => {
        console.error('Error loading user credits:', error);
        this.currentUserCredits = 0;
      }
    });
  }

  selectPlan(credits: number, duration: number): void {
    this.selectedPlan = { credits, duration };
    this.selectedProduct = null; // Reset product selection
  }

  selectProduct(product: Product): void {
    this.selectedProduct = product;
  }

  cancelSelection(): void {
    this.selectedPlan = null;
    this.selectedProduct = null;
  }

  purchaseCredits(): void {
    if (!this.selectedPlan || !this.selectedProduct) {
      return;
    }

    // Call the buy credits API with product selection
    this.userService.buyCredits(this.selectedPlan.credits, this.selectedProduct!.id).subscribe({
      next: (response: any) => {
        alert(`Crdits achets avec succs! ${this.selectedPlan!.credits} crdits pour le produit "${this.selectedProduct!.name}"`);
        // Update current credits
        this.currentUserCredits = response.user.credits;
        // Reset selections
        this.selectedPlan = null;
        this.selectedProduct = null;
        // Reload products to reflect changes
        this.loadUserProducts();
      },
      error: (error) => {
        console.error('Error purchasing credits:', error);
        alert(`Erreur: ${error.error?.error || 'Erreur lors de l\'achat des crdits'}`);
      }
    });
  }
}
