import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from '../../services/user.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html'
})
export class RegisterComponent {
  registerForm: FormGroup;
  loading = false;
  errorMessage = '';
  cities = ['Dakar', 'This', 'Saint-Louis', 'Kaolack', 'Ziguinchor'];

  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private router: Router
  ) {
    this.registerForm = this.fb.group({
      firstName: ['', [Validators.required, Validators.minLength(1)]],
      lastName: ['', [Validators.required, Validators.minLength(1)]],
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(6)]],
      confirmPassword: ['', [Validators.required]],
      phone: ['', [Validators.required]],
      whatsappLink: [''],
      city: ['', [Validators.required]]
    }, { validators: this.passwordMatchValidator });
  }

  passwordMatchValidator(form: FormGroup) {
    const password = form.get('password');
    const confirmPassword = form.get('confirmPassword');

    if (password && confirmPassword && password.value !== confirmPassword.value) {
      confirmPassword.setErrors({ mismatch: true });
      return { mismatch: true };
    }

    return null;
  }

  onSubmit() {
    if (this.registerForm.valid) {
      this.loading = true;
      this.errorMessage = '';

      const formData = this.registerForm.value;

      const userData = {
        email: formData.email,
        password: formData.password,
        firstName: formData.firstName,
        lastName: formData.lastName,
        phone: formData.phone,
        whatsappLink: formData.whatsappLink || undefined,
        city: formData.city || undefined
      };

      this.userService.register(userData).subscribe({
        next: (response: any) => {
          this.loading = false;
          if (response.message === 'Seller created successfully') {
            this.router.navigate(['/login']);
          } else {
            this.errorMessage = response.error || 'Erreur lors de l\'inscription';
          }
        },
        error: (error: any) => {
          this.loading = false;
          console.error('Registration error:', error);
          if (error.error && error.error.errors && Array.isArray(error.error.errors)) {
            // Validation errors from express-validator
            this.errorMessage = error.error.errors.map((err: any) => err.message).join(', ');
          } else if (error.error && error.error.error) {
            // Other backend errors
            this.errorMessage = error.error.error;
          } else {
            this.errorMessage = 'Erreur lors de l\'inscription. Vrifiez vos informations.';
          }
        }
      });
    } else {
      this.errorMessage = 'Veuillez remplir tous les champs obligatoires correctement.';
    }
  }

  goToLogin() {
    this.router.navigate(['/login']);
  }
}