import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserService } from '../../services/user.service';
import { Router, NavigationEnd } from '@angular/router';
import { Product } from '../../models/product.model';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ChartData, ChartOptions } from 'chart.js';

interface User {
  id: string;
  email: string;
  firstName: string;
  lastName: string;
  role: string;
}

interface Stats {
   totalProducts: number;
   pendingProducts: number;
   approvedProducts: number;
   rejectedProducts: number;
   totalSellers: number;
   vipProducts: number;
 }

@Component({
  selector: 'app-dashboard-admin',
  templateUrl: './dashboard-admin.component.html',
  styleUrls: []
})
export class DashboardAdminComponent implements OnInit, OnDestroy {
  currentUser: User | null = null;
  stats: Stats = {
    totalProducts: 0,
    pendingProducts: 0,
    approvedProducts: 0,
    rejectedProducts: 0,
    totalSellers: 0,
    vipProducts: 0
  };
  pendingProducts: Product[] = [];
  showPendingProducts = false;
  loading = false;
  sellers: any[] = [];
  loadingSellers = false;
  private routerSubscription: Subscription = new Subscription();

  // Chart data
  barChartData: ChartData<'bar'> = {
    labels: ['En attente', 'Valides', 'Refuses'],
    datasets: [{
      data: [0, 0, 0],
      backgroundColor: ['#fbbf24', '#10b981', '#ef4444'],
      borderColor: ['#f59e0b', '#059669', '#dc2626'],
      borderWidth: 1
    }]
  };

  barChartOptions: ChartOptions<'bar'> = {
    responsive: true,
    plugins: {
      legend: {
        display: false
      },
      title: {
        display: true,
        text: 'Statut des Produits'
      }
    },
    scales: {
      y: {
        beginAtZero: true
      }
    }
  };

  pieChartData: ChartData<'pie'> = {
    labels: ['Produits Ajouts', 'Produits VIP'],
    datasets: [{
      data: [0, 0],
      backgroundColor: ['#3b82f6', '#eab308'],
      borderColor: ['#2563eb', '#ca8a04'],
      borderWidth: 1
    }]
  };

  pieChartOptions: ChartOptions<'pie'> = {
    responsive: true,
    plugins: {
      legend: {
        position: 'bottom'
      },
      title: {
        display: true,
        text: 'Rpartition des Produits'
      }
    }
  };


  constructor(private userService: UserService, private router: Router) {}

  ngOnInit() {
    this.loadCurrentUser();
    this.loadStats();
    this.loadPendingProducts();
    this.loadSellers();

    // Listen to route changes to reload sellers when navigating to /admin/vendeurs
    this.routerSubscription = this.router.events
      .pipe(filter((event): event is NavigationEnd => event instanceof NavigationEnd))
      .subscribe((event) => {
        if (event.url === '/admin/vendeurs') {
          this.loadSellers();
        }
      });
  }

  ngOnDestroy() {
    this.routerSubscription.unsubscribe();
  }

  loadCurrentUser() {
    this.userService.getCurrentUser().subscribe({
      next: (response) => {
        this.currentUser = response.user;
      },
      error: () => {
        this.router.navigate(['/login']);
      }
    });
  }

  loadStats() {
    // Calculate stats from all products in the database
    this.userService.getPendingProducts().subscribe({
      next: (response) => {
        const allProducts = response.products;
        this.stats.totalProducts = allProducts.length;
        this.stats.pendingProducts = allProducts.filter((p: any) => p.status === 'PENDING').length;
        this.stats.approvedProducts = allProducts.filter((p: any) => p.status === 'APPROVED').length;
        this.stats.rejectedProducts = allProducts.filter((p: any) => p.status === 'REJECTED').length;
        this.stats.vipProducts = allProducts.filter((p: any) => p.vipStatus === 1).length;
        this.stats.totalSellers = this.sellers.length;

        // Update charts with new data
        this.updateCharts();
      },
      error: (error) => {
        console.error('Error loading stats:', error);
        // Fallback values
        this.stats.pendingProducts = this.pendingProducts.length;
        this.stats.totalProducts = this.pendingProducts.length;
        this.stats.approvedProducts = 0;
        this.stats.rejectedProducts = 0;
        this.stats.vipProducts = 0;
        this.stats.totalSellers = this.sellers.length;
        this.updateCharts();
      }
    });
  }

  updateCharts() {
    // Update bar chart data
    this.barChartData = {
      ...this.barChartData,
      datasets: [{
        ...this.barChartData.datasets[0],
        data: [this.stats.pendingProducts, this.stats.approvedProducts, this.stats.rejectedProducts]
      }]
    };

    // Update pie chart data
    this.pieChartData = {
      ...this.pieChartData,
      datasets: [{
        ...this.pieChartData.datasets[0],
        data: [this.stats.totalProducts - this.stats.vipProducts, this.stats.vipProducts]
      }]
    };
  }

  loadPendingProducts() {
    this.userService.getPendingProducts().subscribe({
      next: (response) => {
        this.pendingProducts = response.products;
        this.loadStats();
      },
      error: (error) => {
        console.error('Error loading pending products:', error);
      }
    });
  }

  loadSellers() {
    console.log(' Dbut du chargement des vendeurs...');
    this.loadingSellers = true;
    this.userService.getAllSellers().subscribe({
      next: (response) => {
        this.loadingSellers = false;
        this.sellers = response.sellers || [];
        console.log(' Vendeurs chargs avec succs:', this.sellers.length, 'vendeurs trouvs');
        console.log(' Dtails des vendeurs:', this.sellers);
      },
      error: (error) => {
        this.loadingSellers = false;
        console.error(' Erreur lors du chargement des vendeurs:', error);
        console.error(' Dtails de l\'erreur:', error.error || error.message);
        this.sellers = [];
      }
    });
  }

  approveProduct(productId: string) {
    this.loading = true;
    this.userService.updateProductStatus(productId, 'APPROVED').subscribe({
      next: () => {
        this.loading = false;
        this.loadPendingProducts(); // Refresh the list
        alert('Produit approuv avec succs!');
      },
      error: (error) => {
        this.loading = false;
        alert('Erreur lors de l\'approbation du produit');
      }
    });
  }

  rejectProduct(productId: string) {
    this.loading = true;
    this.userService.updateProductStatus(productId, 'REJECTED').subscribe({
      next: () => {
        this.loading = false;
        this.loadPendingProducts(); // Refresh the list
        alert('Produit rejet!');
      },
      error: (error) => {
        this.loading = false;
        alert('Erreur lors du rejet du produit');
      }
    });
  }

  getPhotoUrl(url: string): string {
    return `http://localhost:3000${url}`;
  }

  onImageError(event: any, photo: any) {
    console.log('Image failed to load:', photo.url);
    // Prevent infinite loop by checking if we're already trying to load the fallback
    if (!event.target.src.includes('no-image.png')) {
      event.target.src = 'assets/images/no-image.png'; // Fallback image
    }
  }

  showAllPhotos(product: any) {
    // For now, just show an alert. In a real app, you'd open a modal
    alert(`Produit "${product.name}" a ${product.photos.length} photos`);
  }

  isActiveRoute(route: string): boolean {
    return this.router.url === route;
  }

  logout() {
    this.userService.logout().subscribe({
      next: () => {
        this.router.navigate(['/login']);
      },
      error: (error) => {
        console.error('Logout error:', error);
        // Even if logout fails, navigate to login
        this.router.navigate(['/login']);
      }
    });
  }

}