import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { UserService } from '../../services/user.service';
import { Router } from '@angular/router';
import { User } from '../../models/user.model';
import { Photo } from '../../models/product.model';

interface Seller {
  id: string;
  firstName: string;
  lastName: string;
  email: string;
  phone?: string;
  whatsappLink?: string;
  city?: string;
}

interface Product {
  id: string;
  name: string;
  description: string;
  price: number;
  category: string;
  mainPhoto: string;
  photos: Photo[];
  seller: Seller;
  isFeatured?: boolean;
  vipStatus?: number;
  featuredUntil?: string;
}

interface ApiResponse {
  products: Product[];
  pagination: {
    page: number;
    limit: number;
    total: number;
    totalPages: number;
  };
}

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: []
})
export class HomeComponent implements OnInit {
  products: Product[] = [];
  currentPage = 1;
  totalPages = 1;
  currentUser: User | null = null;
  isLoggedIn = false;
  showCallModal = false;
  callPhoneNumber = '';
  searchQuery = '';
  selectedCategory = '';
  selectedCity = '';
  selectedPriceRange = '';
  showProductModal = false;
  selectedProduct: Product | null = null;

  private readonly API_URL = 'http://localhost:3000/api/auth';

  constructor(private http: HttpClient, private userService: UserService, private router: Router) {}

  ngOnInit() {
    this.loadProducts();
    this.checkAuthStatus();
  }

  checkAuthStatus() {
    this.isLoggedIn = this.userService.isLoggedIn();
    if (this.isLoggedIn) {
      this.userService.getCurrentUser().subscribe({
        next: (response) => {
          this.currentUser = response.user;
        },
        error: () => {
          this.isLoggedIn = false;
          this.currentUser = null;
        }
      });
    }
  }

  logout() {
    this.userService.logout().subscribe({
      next: () => {
        this.isLoggedIn = false;
        this.currentUser = null;
        this.router.navigate(['/login']);
      },
      error: () => {
        // Even if logout fails, clear local state
        this.isLoggedIn = false;
        this.currentUser = null;
        localStorage.removeItem('auth_token');
        this.router.navigate(['/login']);
      }
    });
  }

  loadProducts(page = 1) {
    this.userService.getProducts(page, 8, this.searchQuery, this.selectedCategory, this.selectedCity, this.selectedPriceRange).subscribe({
      next: (response) => {
        console.log('Products loaded:', response.products);
        // Remove duplicates based on product id using Map for efficiency
        const uniqueProducts = new Map<string, Product>();
        response.products.forEach((product: Product) => {
          console.log('Product:', product.name, 'Main photo:', product.mainPhoto);
          if (!uniqueProducts.has(product.id)) {
            uniqueProducts.set(product.id, product);
          }
        });
        this.products = Array.from(uniqueProducts.values());
        this.currentPage = response.pagination.page;
        this.totalPages = response.pagination.totalPages;
      },
      error: (error) => {
        console.error('Error loading products:', error);
        // No fallback data - products will remain empty if API fails
        this.products = [];
      }
    });
  }

  onSearchInput() {
    if (this.searchQuery && this.searchQuery.length >= 2) {
      this.currentPage = 1;
      this.loadProducts();
    } else if (!this.searchQuery || this.searchQuery.length === 0) {
      this.currentPage = 1;
      this.loadProducts();
    }
  }

  onFilterChange() {
    this.currentPage = 1;
    this.loadProducts();
  }

  callSeller(phone: string) {
    this.callPhoneNumber = phone;
    this.showCallModal = true;
  }

  closeCallModal() {
    this.showCallModal = false;
    this.callPhoneNumber = '';
  }

  whatsappSeller(link: string) {
    let whatsappUrl = link;
    if (!whatsappUrl.includes('?text=')) {
      whatsappUrl += (whatsappUrl.includes('?') ? '&' : '?') + 'text=Bonjour, je suis intress par votre produit.';
    }
    window.open(whatsappUrl, '_blank');
  }

  emailSeller(email: string) {
    window.location.href = `mailto:${email}?subject=Intrt pour votre produit&body=Bonjour, je suis intress par votre produit.`;
  }

  nextPage() {
    if (this.currentPage < this.totalPages) {
      this.loadProducts(this.currentPage + 1);
    }
  }

  prevPage() {
    if (this.currentPage > 1) {
      this.loadProducts(this.currentPage - 1);
    }
  }

  openProductModal(product: Product) {
    this.selectedProduct = product;
    this.showProductModal = true;
  }

  closeProductModal() {
    this.showProductModal = false;
    this.selectedProduct = null;
  }

  onImageError(event: any) {
    console.error('Image failed to load:', event.target.src);
    // Prevent infinite loop by checking if we're already trying to load the fallback
    if (!event.target.src.includes('no-image.png')) {
      event.target.src = 'http://localhost:3000/uploads/no-image.png';
    } else {
      console.error('Fallback image also failed to load');
    }
  }
}